<template>
  <v-layout column align-center >
    <v-flex text-xs-center text-md-center text-lg-center>
      <vue-typer
        :text='["Learn... Trade... Earn..."]'
        :repeat='Infinity'
        :shuffle='false'
        initial-action='typing'
        :pre-type-delay='70'
        :type-delay='70'
        :pre-erase-delay='2000'
        :erase-delay='250'
        erase-style='select-all'
        :erase-on-complete='false'
        caret-animation='blink'
        class="display-4"
      ></vue-typer>
    </v-flex>
  </v-layout>
</template>
<script>
import { VueTyper } from 'vue-typer'
export default {
  components: {
    VueTyper
  }
}
</script>

